@charset "UTF-8";
/* ------------- icon ------------- */
.gio-icon {
  display: inline-block;
  width: 14px;
  height: 14px;
}
.icon-button {
  padding: 7px;
  background-color: #4CAAF5;
  border-radius: 4px;
  border: 0;
  min-width: 33px;
  min-height: 33px;
  text-align: center;
}
.icon-button + .icon-button {
  margin-left: 6px;
}
.icon-button.text-btn {
  letter-spacing: 1px;
}
.icon-button.btn--gray {
  background: #788088;
  color: #FFF;
}
.icon-button.icon__edit {
  background-color: #788088;
}
.icon-button.icon__edit.icon__edit--diabled {
  background-color: #CECECE;
}
.icon-button.icon__delete {
  background-color: #F77878;
  color: #FFF;
}
.icon-button.icon__upload {
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  color: #FFF;
  padding: 6px 20px;
}
.icon-button.icon__upload:hover, .icon-button.icon__upload:active, .icon-button.icon__upload:focus {
  background-color: #4CAAF5;
}
.icon-button.icon__upload-disabled {
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  padding: 6px 20px;
  color: #999;
  background-color: #CECECE;
}
.icon-button.icon__upload-disabled:hover, .icon-button.icon__upload-disabled:active, .icon-button.icon__upload-disabled:focus {
  color: #999;
  background-color: #CECECE;
}
.gio-icon__home {
  background: url(../img/image_home.c6072423.svg) no-repeat center;
}
.gio-icon__nextPage {
  background: url(../img/image_nextPage.ab1c48ff.svg) no-repeat center;
  cursor: pointer;
}
.gio-icon__add {
  background: url(../img/button_add.bb274351.svg) no-repeat center;
}
.gio-icon__download {
  background: url(../img/button_download.9425800a.svg) no-repeat center;
}
.gio-icon__search {
  background: url(../img/button_search.1b8928a4.svg) no-repeat center;
}
.icon-button__img {
  display: block;
  margin: 0 auto;
  background-size: auto 19px;
}
.ant-input-group-addon .ant-btn {
  padding: 0 10px;
  background-color: #4CAAF5;
}
.ant-input-group-addon .ant-btn:hover {
  background-color: #4CAAF5;
}

/* ------------- Basic ------------- */
html {
  height: 100%;
}
body {
  min-height: 100%;
}
p {
  margin-bottom: 0;
}

/* ------------- 彈窗 ------------- */
.ant-modal-mask {
  background-color: rgba(0, 0, 0, 0.6);
}
.ant-modal-content {
  border-radius: 4px;
}
.ant-modal-content .ant-modal-confirm-body-wrapper {
  display: flex;
  flex-direction: column;
}
.ant-modal-content .ant-modal-confirm-title {
  color: #000;
}
.ant-modal-content .ant-modal-confirm-content {
  color: #595959;
}
.ant-modal-content .ant-modal-confirm-btns {
  margin-top: 50px;
  margin-left: auto;
}
.ant-modal-content .icon__custom {
  width: 22px;
  height: 22px;
  float: left;
  margin-right: 10px;
  border-radius: 50vh;
}
.ant-modal-content .icon__custom ~ .ant-modal-confirm-content {
  margin-left: 33px;
}
.ant-modal-content .icon__error {
  background: url(../img/image_alert.578a8571.svg) no-repeat center #E06262;
}
.ant-modal-content .icon__delete {
  background: url(../img/image_deletAlert.5be441e5.svg) no-repeat center #E06262;
}
.ant-modal-content .ant-btn {
  border-radius: 4px;
  color: #000;
}
.ant-modal-content .ant-btn:hover, .ant-modal-content .ant-btn:active, .ant-modal-content .ant-btn:focus {
  border-color: #D9D9D9;
}
.ant-modal-content .ant-btn-confrim {
  background-color: #4CAAF5;
  color: #FFF;
}
.ant-modal-content .ant-btn-warning {
  color: #FC0000;
}

/* ------------- main ------------- */
.ant-layout {
  background: #FFF;
}
.ant-layout.bg__blue {
  background-color: #F2F8FF;
}
.main-header {
  min-height: 33px;
  margin-bottom: 10px;
}
.main-container {
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-title {
  font-size: 18px;
  margin-bottom: 0;
  line-height: 33px;
  font-weight: 600;
}
.ant-divider-dashed {
  border-color: #DFE6EC;
}
.main-HeaderBtn__group {
  text-align: center;
  margin-bottom: 20px;
}
.main-HeaderInfo {
  margin-bottom: 10px;
}
.main-HeaderInfo .info__item {
  flex: 1;
  background-color: #F2F8FF;
  font-size: 16px;
  padding: 11px 17px;
}
.main-HeaderInfo .info__item + .info__item {
  margin-left: 10px;
}
.main-HeaderInfo .info__item__title {
  font-weight: bold;
}
.main-HeaderInfo .locker__item {
  flex: 0 0 150px;
}
.main-EditGroup .main-Edit__leftBlock,
.main-EditGroup .main-Edit__middleBlock,
.main-EditGroup .main-Edit__rightBlock {
  flex: 1 1 0;
}
.main-EditGroup .main-Edit__leftBlock {
  text-align: right;
}
.main-EditGroup__header {
  display: flex;
  background-color: #7CACD3;
  color: #FFF;
}
.main-EditGroup__header .main-Edit__leftBlock,
.main-EditGroup__header .main-Edit__middleBlock,
.main-EditGroup__header .main-Edit__rightBlock {
  padding: 14px 7.5px;
}
.main-EditGroup__body .main-Edit__leftBlock,
.main-EditGroup__body .main-Edit__middleBlock,
.main-EditGroup__body .main-Edit__rightBlock,
.main-EditGroup__body .main-Edit__title {
  padding: 10px 7.5px;
}
.main-EditGroup__body .main-Edit__item-formControl,
.main-EditGroup__body .main-Edit__item-formType {
  padding-top: 3px;
  padding-bottom: 3px;
}
.main-Edit__section {
  padding: 10px 0;
  border-bottom: 1px dashed #CECECE;
}
.main-Edit__section:last-child {
  padding-bottom: 20px;
  border-bottom: 1px solid #788088;
}
.main-Edit__titleWrap {
  display: flex;
}
.main-Edit__title {
  padding: 14px 7.5px;
  color: #4CAAF5;
  font-weight: 500;
  font-size: 16px;
  width: calc(100% / 3);
  text-align: right;
}
.main-Edit__item {
  display: flex;
  align-items: center;
}

/* ------------- table ------------- */
.result__table {
  margin-bottom: 16px;
}
.result__table .ant-table {
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #CECECE;
  overflow-x: auto;
  font-size: 16px;
}
.result__table .ant-table th, .result__table .ant-table td {
  word-break: keep-all;
}
.result__table .ant-table .ant-table-thead {
  background: #7CACD3;
}
.result__table .ant-table .ant-table-thead th {
  color: #FFF;
  padding: 8px 15px !important;
}
.result__table .ant-table .ant-table-content .ant-table-body {
  margin: 0;
}
.result__table .ant-table .ant-table-tbody > tr > td {
  border-bottom: 1px solid #F5F5F5;
  padding: 8px 15px !important;
}

/* ------------- pagination ------------- */
.ant-table-pagination.ant-pagination {
  display: flex;
  align-items: center;
}
.ant-table-pagination.ant-pagination .ant-pagination-item {
  border-color: #DFE6EC;
  line-height: calc(30px - 2px);
  width: 30px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin-left: 4px;
  margin-right: 4px;
}
.ant-table-pagination.ant-pagination .ant-pagination-item:focus a, .ant-table-pagination.ant-pagination .ant-pagination-item:hover a {
  color: unset;
}
.ant-table-pagination.ant-pagination .ant-pagination-item:focus:not(.ant-pagination-item-active), .ant-table-pagination.ant-pagination .ant-pagination-item:hover:not(.ant-pagination-item-active) {
  border-color: #4CAAF5;
}
.ant-table-pagination.ant-pagination .ant-pagination-item:focus:not(.ant-pagination-item-active) a, .ant-table-pagination.ant-pagination .ant-pagination-item:hover:not(.ant-pagination-item-active) a {
  color: #4CAAF5;
}
.ant-table-pagination.ant-pagination .ant-pagination-item.ant-pagination-item-active {
  background-color: #4CAAF5;
  color: #FFF;
}
.ant-table-pagination.ant-pagination .ant-pagination-item:not(.ant-pagination-item-active) {
  border-color: #DFE6EC;
}
.ant-table-pagination.ant-pagination .ant-pagination-prev,
.ant-table-pagination.ant-pagination .ant-pagination-next {
  line-height: 30px;
  height: 30px;
  width: 30px;
  margin-left: 4px;
  margin-right: 4px;
}
.ant-table-pagination.ant-pagination .ant-pagination-prev .ant-pagination-item-link,
.ant-table-pagination.ant-pagination .ant-pagination-next .ant-pagination-item-link {
  border-color: #DFE6EC;
  border-radius: 4px;
}
.ant-table-pagination.ant-pagination .ant-pagination-prev .ant-pagination-item-link .anticon,
.ant-table-pagination.ant-pagination .ant-pagination-next .ant-pagination-item-link .anticon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.ant-table-pagination.ant-pagination .ant-pagination-prev:not(.ant-pagination-disabled) .ant-pagination-item-link:focus a, .ant-table-pagination.ant-pagination .ant-pagination-prev:not(.ant-pagination-disabled) .ant-pagination-item-link:hover a,
.ant-table-pagination.ant-pagination .ant-pagination-next:not(.ant-pagination-disabled) .ant-pagination-item-link:focus a,
.ant-table-pagination.ant-pagination .ant-pagination-next:not(.ant-pagination-disabled) .ant-pagination-item-link:hover a {
  color: #4CAAF5;
}
.ant-table-pagination.ant-pagination .ant-pagination-prev:not(.ant-pagination-disabled) .ant-pagination-item-link:focus:not(.ant-pagination-item-active), .ant-table-pagination.ant-pagination .ant-pagination-prev:not(.ant-pagination-disabled) .ant-pagination-item-link:hover:not(.ant-pagination-item-active),
.ant-table-pagination.ant-pagination .ant-pagination-next:not(.ant-pagination-disabled) .ant-pagination-item-link:focus:not(.ant-pagination-item-active),
.ant-table-pagination.ant-pagination .ant-pagination-next:not(.ant-pagination-disabled) .ant-pagination-item-link:hover:not(.ant-pagination-item-active) {
  border-color: #4CAAF5;
}
.ant-table-pagination.ant-pagination .ant-pagination-options {
  margin-left: 15px;
}
.ant-table-pagination.ant-pagination .ant-pagination-options .ant-select {
  height: 30px;
}
.ant-table-pagination.ant-pagination .ant-pagination-options .ant-select-selection--single {
  height: 30px;
  border-radius: 4px;
}
.ant-table-pagination.ant-pagination .ant-pagination-options .ant-select-selection__rendered {
  line-height: 30px;
  margin-right: 30px;
}
.ant-table-pagination.ant-pagination .ant-pagination-options input {
  border-color: #DFE6EC;
  border-radius: 4px;
  height: 30px;
}

/* ------------- form ------------- */
.formContent {
  background-color: white;
  border-radius: 10px;
  padding: 20px 0 40px;
  max-width: 100%;
}
.gioFormGroup .ant-form-item-label {
  padding-bottom: 5px;
  padding-top: 5px;
}
.gioFormGroup .ant-form-item-label > label {
  font-size: 16px;
  font-weight: 600;
}
.gioFormGroup .ant-input-group-addon > button {
  border-radius: 4px;
}
.gioFormGroup > .ant-form-item {
  padding-bottom: 0;
  margin-bottom: 0;
  font-size: 16px;
}
.gioFormGroup > .ant-form-item + .ant-form-item {
  margin-top: 20px;
}
.gioFormGroup .ant-form-item-control .ant-form-item {
  margin-bottom: 0;
  padding-bottom: 0;
}
.gioFormGroup .ant-btn > .anticon {
  line-height: 30px;
}
.ant-form-item-label {
  font-weight: 500;
}
.formW275 {
  max-width: 275px;
  margin: 0 auto;
}
.formW532 {
  max-width: 532px;
  margin: 0 auto;
}
.formW550 {
  max-width: 550px;
  margin: 0 auto;
}
.formW570 {
  max-width: 570px;
  margin: 0 auto;
}
.formW720 {
  max-width: 720px;
  margin: 0 auto;
}
.formW860 {
  max-width: 860px;
  margin: 0 auto;
}
.formW900 {
  max-width: 900px;
  margin: 0 auto;
}
.formW1088 {
  max-width: 1088px;
  margin: 0 auto;
}
.form-item__bg--blue {
  background-color: #E6F4FF;
}
.form-item__bg--blue.ant-form-item {
  padding-bottom: 0;
  margin-bottom: 23px;
}
.form-item__bg--blue + .message--error {
  position: relative;
  top: -20px;
}
.ant-radio-wrapper,
.ant-checkbox-wrapper {
  margin-right: 22px;
}
.search-header {
  min-height: 25px;
  margin-bottom: 20px;
}
.search-footer {
  margin-top: 40px;
}
.form-label-slot {
  display: flex;
  align-items: center;
}
.form-label-slot .anticon-info-circle {
  margin-left: 3px;
}
input::-moz-placeholder {
  color: rgba(153, 153, 153, 0.7);
}
input:-ms-input-placeholder {
  color: rgba(153, 153, 153, 0.7);
}
input::placeholder {
  color: rgba(153, 153, 153, 0.7);
}
.ant-radio-wrapper > span:not(.ant-radio) {
  display: inline-block;
  min-width: 3.5rem;
}
.ant-checkbox-wrapper > span:not(.ant-checkbox) {
  display: inline-block;
  min-width: 3.5rem;
}
.message--error {
  color: #FC0000;
  margin-top: 2px;
  font-size: 14px;
}
.ant-upload-hint {
  color: #FC0000;
  padding-left: 10px;
  font-size: 12px;
}

/* ------------- button ------------- */
.confirm__button-group {
  margin-top: 50px;
  margin-bottom: 20px;
}
.confirm__button {
  min-width: 150px;
  border-radius: 50vh;
  font-size: 16px;
  padding: 12px 20px;
  border: 0;
}
.confirm__button-submit {
  color: #F0F8FF;
  background: #4CAAF5 0% 0% no-repeat padding-box;
  border: 3px solid transparent;
  box-sizing: border-box;
}
.confirm__button-submit:hover {
  background-color: #4CAAF5;
  border-color: #4CAAF566;
}
.confirm__button-download {
  color: #4CAAF5;
  background: #FFF 0% 0% no-repeat padding-box;
  border: #4CAAF5 1px solid;
  box-shadow: 0 0 0 3px transparent;
}
.confirm__button-download:hover {
  background-color: #FFF;
  box-shadow: 0 0 0 3px #4CAAF566;
}
.confirm__button-cancel {
  color: #4CAAF5;
  background: #FFF 0% 0% no-repeat padding-box;
  border: #4CAAF5 1px solid;
  box-shadow: 0 0 0 3px transparent;
  margin-right: 10px;
}
.confirm__button-cancel:hover {
  background-color: #FFF;
  box-shadow: 0 0 0 3px #4CAAF566;
}
.card-confirm__button-group {
  margin-top: 40px;
  margin-bottom: 20px;
}
.card-header-btn {
  font-size: 16px;
  padding: 5px 18px;
  background-color: #4CAAF5;
  border-radius: 4px;
  border: 0;
  text-align: center;
}
.card-header-btn.text-btn {
  color: #FFF;
}
.ant-radio-button-wrapper {
  padding-left: 35px;
  padding-right: 35px;
  font-size: 16px;
}
.ant-radio-button-wrapper:first-child {
  border-radius: 4px 0 0 4px;
}
.ant-radio-button-wrapper:last-child {
  border-radius: 0 4px 4px 0;
}
.btn__bg--secondary {
  background-color: #788088;
  color: #FFF;
}
.btn__bg--danger {
  background-color: #F5F5F5;
  color: #FFF;
}
.btn__bg--primary {
  background-color: #4CAAF5;
  color: #FFF;
}
.btn__bg--disabled {
  background-color: #CECECE !important;
  color: #8C8C8C !important;
}

/* ------------- 客製化檔案上傳列表 ------------- */
.upload-list {
  font-size: 14px;
}
.upload-list .upload-list-item {
  position: relative;
  align-items: center;
  padding: 0 3px;
  margin-top: 8px;
}
.upload-list .upload-list-item:hover {
  background-color: #e6f7ff;
}
.upload-list .upload-list-item:hover .icon-deleteGray {
  opacity: 1;
}
.upload-list .upload-list-item .icon-paperclip {
  width: 13px;
  margin: 0;
}
.upload-list .upload-list-item .icon-deleteGray {
  position: absolute;
  right: 3px;
  cursor: pointer;
  width: 13px;
  opacity: 0;
}

/* ------------- antd $notification ------------- */
.notification__success {
  width: 250px;
  margin-left: auto;
}
.notification__success .ant-notification-close-x {
  display: none;
}

/* ------------- page內 卡片形式 ------------- */
.page__card {
  width: 100%;
  min-height: 200px;
  background: #FFF;
  border-radius: 10px;
}
.page__card .gioFormGroup {
  padding: 35px 7.5px;
  font-size: 16px;
}
.page__card .gioConfirmGroup {
  padding: 10px 7.5px 35px;
  font-size: 16px;
}
.page__card .ant-form-item-control {
  padding: 0 16px;
}
.page__card .ant-row .ant-form-item-control {
  padding: 0;
}
.page__card .result__info {
  padding: 5px 0;
}
.page__card .ant-radio-group,
.page__card .ant-checkbox-group {
  padding: 5px 0;
}
.gioConfirmGroup .row-confirm-item {
  margin-top: 20px;
  line-height: 1.5;
}
.gioConfirmGroup .confirm-item__title {
  padding: 5px 16px;
  text-align: right;
}
.gioConfirmGroup .result__info {
  padding: 5px 16px;
}

/* ------------- Antd 全域覆寫區 ------------- */
.ant-switch-inner {
  margin-left: 20px;
  margin-right: 4px;
}
.ant-switch-checked {
  background-color: #4CAAF5;
}
.ant-switch-checked .ant-switch-inner {
  margin-right: 20px;
  margin-left: 4px;
}
.ant-input,
.ant-input-number,
.ant-select-selection {
  border-radius: 4px;
  overflow: hidden;
}
.ant-form label,
.ant-form-item,
.ant-input,
.ant-input-number-input,
.ant-select-selection-selected-value {
  font-size: 16px;
}
.ant-btn.ant-btn-warning {
  background-color: #F5F5F5;
}
.ant-upload-list-item-name {
  padding-left: 10px;
}
.has-error .ant-checkbox-inner,
.has-error .ant-radio-inner,
.has-error .mx-input {
  border-color: #FC0000;
}
.has-error .ant-checkbox-wrapper:hover .ant-checkbox-inner,
.has-error .ant-checkbox:hover .ant-checkbox-inner {
  border-color: #FC0000;
}
.has-error .ant-checkbox-input:focus + .ant-checkbox-inner {
  border-color: #FC0000;
}
.ant-input-clear-icon, .ant-input-textarea-clear-icon {
  color: rgba(0, 0, 0, 0.25);
}
form textarea.ant-input {
  margin-bottom: 0;
}
.ant-pagination-options {
  display: flex;
}
#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#app .headerSpace {
  padding-top: 80px;
}